import React, { useState } from "react";

export default function App() {
  const questions = [
    {
      questionText: "남자친구의 생일은 언제일까요?",
      answerOptions: [
        { answerText: "4월 29일", isCorrect: false },
        { answerText: "5월 19일", isCorrect: false },
        { answerText: "4월 27일", isCorrect: true },
        { answerText: "6월 12일", isCorrect: false },
      ],
    },
    {
      questionText: "남자친구의 혈액형은 무엇인가요??",
      answerOptions: [
        { answerText: "A형", isCorrect: false },
        { answerText: "O형", isCorrect: true },
        { answerText: "AB형", isCorrect: false },
        { answerText: "B형", isCorrect: false },
      ],
    },
    {
      questionText: "남자친구의 가장 처음으로 했던 전공은 무엇일까요?",
      answerOptions: [
        { answerText: "경제학", isCorrect: true },
        { answerText: "전기공학", isCorrect: false },
        { answerText: "전자공학", isCorrect: false },
        { answerText: "컴퓨터공학", isCorrect: false },
      ],
    },
    {
      questionText: "남자친구가 여행가지 않았던 곳은?",
      answerOptions: [
        { answerText: "홍콩", isCorrect: false },
        { answerText: "호주", isCorrect: true },
        { answerText: "미국", isCorrect: false },
        { answerText: "중국", isCorrect: false },
      ],
    },
  ];

  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [showScore, setShowScore] = useState(false);
  const [score, setScore] = useState(0);

  const handleAnswerOptionClick = (isCorrect) => {
    if (isCorrect) {
      setScore(score + 1);
    }

    const nextQuestion = currentQuestion + 1;
    if (nextQuestion < questions.length) {
      setCurrentQuestion(nextQuestion);
    } else {
      setShowScore(true);
    }
  };
  return (
    <div className="app">
      {showScore ? (
        <div className="score-section">
          당신은 {questions.length}개중에 {score}개를 맞혔습니다.
        </div>
      ) : (
        <>
          <div className="question-section">
            <div className="question-count">
              <span>Question {currentQuestion + 1}</span>/{questions.length}
            </div>
            <div className="question-text">
              {questions[currentQuestion].questionText}
            </div>
          </div>
          <div className="answer-section">
            {questions[currentQuestion].answerOptions.map((answerOption) => (
              <button
                onClick={() => handleAnswerOptionClick(answerOption.isCorrect)}
              >
                {answerOption.answerText}
              </button>
            ))}
          </div>
        </>
      )}
    </div>
  );
}
